import "./SideBar.css"

// icons: https://material-ui.com/components/material-icons/#material-icons
import {LineStyle, Timeline, 
    TrendingUp, PersonOutline, Storefront,
    AttachMoney, BarChart,MailOutline,DynamicFeed,
    ChatBubbleOutline, WorkOutline, Report} from '@material-ui/icons'
import { Link, NavLink } from "react-router-dom"

export default function SideBar() {
    return (
        <div className="sidebar">
            <div className="sidebarWrapper">
                <div className="sidebarMenu">

                    <h3 className="sidebarTitle">Dashboard</h3>
                    <ul className="sidebarList">

                        <Link to="/" className="link">
                            <li className="sidebarListItem">         
                            <LineStyle className="sidebarIcon"/>
                                Home
                            </li>
                        </Link>

                        <li className="sidebarListItem">
                        <Timeline  className="sidebarIcon"/>
                            Analytics
                        </li>

                        <li className="sidebarListItem">
                        <TrendingUp  className="sidebarIcon"/>
                            Sales
                        </li>
                    </ul>

                    <h3 className="sidebarTitle">Quick Menu</h3>
                    <ul className="sidebarList">
                        <NavLink to="/users" className="link"  activeClassName="selected">
                            <li className="sidebarListItem">
                            <PersonOutline className="sidebarIcon"/>
                                User
                            </li>
                        </NavLink>

                        <Link to="/products" className="link">  
                            <li className="sidebarListItem">
                            <Storefront className="sidebarIcon"/>
                                Products
                            </li>
                        </Link>

                        <li className="sidebarListItem">
                        <AttachMoney  className="sidebarIcon"/>
                            Transactions
                        </li>

                        <li className="sidebarListItem">
                        <BarChart  className="sidebarIcon"/>
                            Reporsts
                        </li>
                    </ul>

                    <h3 className="sidebarTitle">Notifications</h3>
                    <ul className="sidebarList">
                        <li className="sidebarListItem">
                        <MailOutline className="sidebarIcon"/>
                            Mail
                        </li>

                        <li className="sidebarListItem">
                        <DynamicFeed className="sidebarIcon"/>
                            Feedback
                        </li>

                        <li className="sidebarListItem">
                        <ChatBubbleOutline  className="sidebarIcon"/>
                            Messages
                        </li>
                    </ul>

                    <h3 className="sidebarTitle">Staff</h3>
                    <ul className="sidebarList">
                        <li className="sidebarListItem">
                        <WorkOutline className="sidebarIcon"/>
                            Manage
                        </li>

                        <li className="sidebarListItem">
                        <Timeline  className="sidebarIcon"/>
                            Analytics
                        </li>

                        <li className="sidebarListItem">
                        <Report  className="sidebarIcon"/>
                            Reports
                        </li>
                    </ul>

                </div>
            </div>
        </div>
    )
}

